import React from "react";
import styles from './Que.module.css'

const Que = () => {
  return (
    <div>
      <div className={styles.main}>
        <div class="row" className={styles.header}>
          <h1>Qu es LudiClass?</h1>
        </div>
        
        <div class="row justify-content-center">
          <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6">
            <div className={styles.maincontent}>
              <p>
                LudiClass es la aplicacin que te permite buscar y compartir
                actividades ldicas para hacer tus clases ms divertidas.
              </p>
              <br />
              
              <p>
                Creemos que el juego nos ayuda a crear experiencias que nos permitan
                <span className={styles.green}>
                  {" "}
                  aprender al mismo tiempo que salimos de la rutina .
                </span>
                <br />  Haz creado actividades? Tienes tu propia versin de una
                dinmica?
              </p>
              <br />
              
              <p>
                <span className={styles.green}>
                  En LudiClass podrs compartir tus ideas con otras personas,
                </span>{" "}
                  para que entre todos cambiemos la manera tradicional de ensear. 
                  <br /> nimate a incluir la ldica en{" "}
                  <span className={styles.green}>cualquier tema</span> que ensees y para{" "}
                  <span className={styles.green}>cualquier edad.</span>
                </p>
              </div>
            </div>
          </div>
      </div>
    </div>
  );
};

export default Que;