import React from "react";
import styles from "./Sugerencias.module.css";
import Instagram from "./Instagram";

export default function Sugerencias() {
  return (
    <div class="container-fluid" className={styles.main}>
      <div class="row justify-content-center">
        <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 d-flex flex-column">
          <h1 className={styles.header}>Comentarios y sugerencias</h1>
          <p className={styles.text}>
            Te ha gustado nuestra idea? Tienes alguna duda o sugerencia?
            <br />
            Quieres que incluyamos algo ms?
          </p>
          <p className={styles.text}>Djanos aqu tu opinin!</p>

          <div className={styles.formulario}>
            <form
              action="https://formspree.io/ludiclass.contacto@gmail.com"
              method="POST"
            >
              {/*name*/}
              <div class="form-group">
                <label htmlFor="name">Nombre</label>
                <input
                  type="text"
                  class="form-control"
                  name="name"
                  id="name"
                  placeholder="Ingresa tu nombre"
                />
              </div>
              {/*email*/}
              <div class="form-group">
                <label htmlFor="email">Correo electrnico</label>
                <input
                  type="email"
                  class="form-control"
                  name="email"
                  id="email"
                  placeholder="Ingresa tu correo"
                />
              </div>
              {/*descripcion*/}
              <div class="form-group">
                <label htmlFor="descripcion">Djanos un mensaje</label>
                <textarea
                  name="descripcion"
                  id="descripcion"
                  class="form-control"
                  rows="5"
                  placeholder="Escribe aqu tu mensaje"
                />
              </div>
              {/*submit*/}
              <button type="submit" class="btn" className={styles.btn}>
                Enviar
              </button>
            </form>
          </div>
        </div>
        <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6">
          <h1 className={styles.header}>Sguenos en Instagram</h1>
          <Instagram />
        </div>
      </div>
    </div>
  );
}
